@font-face {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/titan-one-v13-latin-regular.eot');
    src: local(''),
        url('../fonts/titan-one-v13-latin-regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/titan-one-v13-latin-regular.woff2') format('woff2'),
        url('../fonts/titan-one-v13-latin-regular.woff') format('woff'),
        url('../fonts/titan-one-v13-latin-regular.ttf') format('truetype'),
        url('../fonts/titan-one-v13-latin-regular.svg#TitanOne') format('svg');
}

